@import url(https://fonts.googleapis.com/css2?family=Cairo&display=swap);
[data-theme="light"] {
  --fontOne: #000;
  --fontTwo: #fff;
  --bg: #572b9f;
  --panel: #fff;
  --switch-o: #000;
  --switch-i: #fff;
}
[data-theme="dark"] {
  --fontOne: #fff;
  --fontTwo: #fff;
  --bg: #000;
  --panel: #572b9f;
  --switch-o: #fff;
  --switch-i: #572b9f;
}

body {
  background-color: var(--bg);
  transition: all 0.5s;
}

.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 28px;
}

.switch input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 34px;
  background-color: var(--switch-i);
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 4px;
  border-radius: 50%;
  background-color: var(--switch-o);
  transition: 0.4s;
}

input:checked + .slider {
  background-color: var(--switch-i);
}

input:checked + .slider:before {
  -webkit-transform: translateX(21px);
  transform: translateX(21px);
}

nav {
  padding: 10px;
  position: sticky;
  top: 0;
  background-color: var(--bg);
  height: 10vh;
}

.nav-list {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.hami {
  cursor: pointer;
}
.line {
  margin: 5px;
  width: 35px;
  height: 5px;
  border-radius: 5px;
  background-color: var(--panel);
}
.icon {
  display: grid;
  place-items: center;
}
.logo {
  height: 50px;
}
#nav-tap {
  position: absolute;
  top: 10vh;
  left: 0;
  height: 90vh;
  width: 100%;
  background-color: var(--bg);
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.hide-tab {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
}

.tab-list {
  list-style: none;
  text-align: center;
  width: 100%;
}
.tab-link {
  padding: 10px 0;
  font-size: 30px;
  font-weight: bold;
}
.tab-link:hover {
  background-color: #ffffff2a;
}

.wrapper {
  width: 100%;
  padding: auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.welcome {
  font: bold 44px "Cairo";
  text-align: center;
}
.about {
  font: bold 30px "Cairo";
  text-align: center;
  color: var(--fontTwo);
}
.follow {
  font: bold 18px "Cairo";
  color: var(--fontTwo);
}
.welcome-text {
  margin: 10px 20px;
  font-weight: bold;
}
.welcome-wrapper {
  min-height: 340px;
  width: 80%;
  margin: 20px auto;
  padding: 10px;
  background-color: var(--panel);
  border-radius: 10px;
}
.about-text {
  margin: 10px 20px;
}
.about-wrapper {
  min-height: 340px;
  width: 80%;
  margin: 20px auto;
  padding: 10px;
  background-color: var(--panel);
  border-radius: 10px;
}
.follow-wrapper {
  min-height: 50px;
  width: 80%;
  display: flex;
  padding: 20px 10px 10px 10px;
  background-color: var(--panel);
  justify-content: space-evenly;
  border-radius: 10px;
}
.follow-wrapper img {
  width: 50px;
  height: 50px;
}
#contact-wrapper {
  width: 80%;
  height: 39pc;
  margin: 40px auto;
  display: flex;
  flex-direction: column;
  padding: 10px;
  padding-bottom: 40px;
  background-color: var(--panel);
  border-radius: 10px;
  align-items: center;
}
.contact-text {
  font: bold 30px "Cairo";
  margin-bottom: 20px;
}

#my-form {
  width: 80%;
}

.form-group {
  display: flex;
  flex-direction: column;
}
.form-group input,
textarea {
  font-size: 18px;
  padding: 5px;
  resize: vertical;
}
.sub-btn {
  margin: 10px;
  margin-left: 0;
  padding: 0 10px;
  font-size: 20px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  color: rgb(32, 32, 32);
}
.sub-btn:hover {
  background-color: rgb(188, 149, 252);
  border: 1px solid black;
  box-shadow: 0 5px 0px 0px rgb(0, 0, 0);
}
.sub-btn:focus {
  margin-top: 13px;
  background-color: rgb(188, 149, 252);
  border: 1px solid black;
  box-shadow: 0 2px 0px 0px rgb(0, 0, 0);
  transition: 0.1s;
}
#my-form-status {
  padding: 10px;
  font-size: 15px;
  text-align: center;
  border-radius: 10px;
  transition: all 0.5s;
}
.hide {
  display: none;
  transition: all 0.5s ease-in-out;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: var(--fontOne);
  font: 24px "Cairo", sans-serif;
  transition: all 0.5s;
}
a {
  text-decoration: none;
}

